import React from 'react';
import {Alert, Button} from 'antd';

import './index.less';

export default function MockerSwitcher(props) {
  const {isDisabled, activeModule, previewResult, updateDisable} = props;

  return (
    <div className="mocker-action">
      {isDisabled ? (
        <Alert
          message=" mock  mock "
          type="warning"
          showIcon
        />
      ) : (
        <Alert message="mock " type="success" showIcon />
      )}

      <Button
        type="primary"
        disabled={isDisabled ? 'disable' : ''}
        icon="profile"
        onClick={previewResult}>
          {activeModule} 
      </Button>

      <Button type={isDisabled ? 'primary' : 'default'} icon="setting" onClick={updateDisable}>
        {isDisabled ? '' : ''} mock 
      </Button>
    </div>
  );
}



